/* Import Google Fonts - optimized for Vietnamese text readability */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&family=Source+Sans+Pro:ital,wght@0,400;0,600;0,700;1,400;1,600&family=JetBrains+Mono:wght@400;500;600&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

/* Modern CSS Custom Properties for consistent color system */
:root {
  /* Typography - Modern, readable fonts optimized for Vietnamese */
  --font-primary: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', system-ui, sans-serif;
  --font-secondary: 'Source Sans Pro', -apple-system, BlinkMacSystemFont, 'Segoe UI', system-ui, sans-serif;
  --font-mono: 'JetBrains Mono', 'SF Mono', 'Monaco', 'Cascadia Code', 'Roboto Mono', 'Courier New', monospace;
  
  /* Font weights */
  --font-light: 300;
  --font-normal: 400;
  --font-medium: 500;
  --font-semibold: 600;
  --font-bold: 700;
  --font-extrabold: 800;
  
  /* Font sizes with better hierarchy */
  --text-xs: 0.75rem;      /* 12px */
  --text-sm: 0.875rem;     /* 14px */
  --text-base: 1rem;       /* 16px */
  --text-lg: 1.125rem;     /* 18px */
  --text-xl: 1.25rem;      /* 20px */
  --text-2xl: 1.5rem;      /* 24px */
  --text-3xl: 1.875rem;    /* 30px */
  --text-4xl: 2.25rem;     /* 36px */
  --text-5xl: 3rem;        /* 48px */
  --text-6xl: 3.75rem;     /* 60px */
  
  /* Line heights optimized for readability */
  --leading-tight: 1.25;
  --leading-snug: 1.375;
  --leading-normal: 1.5;
  --leading-relaxed: 1.625;
  --leading-loose: 2;

  /* Primary Colors - Harmonious blue-purple spectrum */
  --color-primary-50: #f0f4ff;
  --color-primary-100: #e0e7ff;
  --color-primary-200: #c7d2fe;
  --color-primary-300: #a5b4fc;
  --color-primary-400: #818cf8;
  --color-primary-500: #6366f1;
  --color-primary-600: #4f46e5;
  --color-primary-700: #4338ca;
  --color-primary-800: #3730a3;
  --color-primary-900: #312e81;
  --color-primary-950: #1e1b4b;

  /* Secondary Colors - Warmer purple accents */
  --color-secondary-50: #faf5ff;
  --color-secondary-100: #f3e8ff;
  --color-secondary-200: #e9d5ff;
  --color-secondary-300: #d8b4fe;
  --color-secondary-400: #c084fc;
  --color-secondary-500: #a855f7;
  --color-secondary-600: #9333ea;
  --color-secondary-700: #7c3aed;
  --color-secondary-800: #6b21a8;
  --color-secondary-900: #581c87;
  --color-secondary-950: #3b0764;

  /* Accent Colors - Complementary warm tones */
  --color-accent-orange: #f97316;
  --color-accent-orange-light: #fb923c;
  --color-accent-orange-dark: #ea580c;
  
  --color-accent-emerald: #10b981;
  --color-accent-emerald-light: #34d399;
  --color-accent-emerald-dark: #059669;
  
  --color-accent-rose: #f43f5e;
  --color-accent-rose-light: #fb7185;
  --color-accent-rose-dark: #e11d48;

  /* Neutral Colors - Warmer grays with slight blue undertone */
  --color-neutral-50: #f8fafc;
  --color-neutral-100: #f1f5f9;
  --color-neutral-200: #e2e8f0;
  --color-neutral-300: #cbd5e1;
  --color-neutral-400: #94a3b8;
  --color-neutral-500: #64748b;
  --color-neutral-600: #475569;
  --color-neutral-700: #334155;
  --color-neutral-800: #1e293b;
  --color-neutral-900: #0f172a;
  --color-neutral-950: #020617;

  /* Gradients */
  --gradient-primary: linear-gradient(135deg, var(--color-primary-600) 0%, var(--color-secondary-600) 100%);
  --gradient-secondary: linear-gradient(135deg, var(--color-secondary-500) 0%, var(--color-accent-orange) 100%);
  --gradient-accent: linear-gradient(135deg, var(--color-accent-emerald) 0%, var(--color-primary-500) 100%);
  --gradient-warm: linear-gradient(135deg, var(--color-accent-orange-light) 0%, var(--color-accent-rose-light) 100%);
  
  /* Background gradients */
  --bg-gradient-light: linear-gradient(135deg, var(--color-primary-50) 0%, var(--color-secondary-50) 50%, var(--color-neutral-50) 100%);
  --bg-gradient-dark: linear-gradient(135deg, var(--color-neutral-900) 0%, var(--color-primary-950) 50%, var(--color-secondary-950) 100%);

  /* Shadows with color */
  --shadow-primary: 0 4px 6px -1px rgba(99, 102, 241, 0.1), 0 2px 4px -1px rgba(99, 102, 241, 0.06);
  --shadow-secondary: 0 10px 15px -3px rgba(168, 85, 247, 0.1), 0 4px 6px -2px rgba(168, 85, 247, 0.05);
  --shadow-accent: 0 20px 25px -5px rgba(99, 102, 241, 0.1), 0 10px 10px -5px rgba(168, 85, 247, 0.04);
  
  /* Transitions */
  --transition-fast: 150ms cubic-bezier(0.4, 0, 0.2, 1);
  --transition-normal: 300ms cubic-bezier(0.4, 0, 0.2, 1);
  --transition-slow: 500ms cubic-bezier(0.4, 0, 0.2, 1);

  /* Focus ring variables */
  --focus-ring-width: 2px;
  --focus-ring-offset: 2px;
  --focus-ring-color: var(--color-primary-500);
}

/* Dark theme overrides */
.dark {
  --color-neutral-50: #0f172a;
  --color-neutral-100: #1e293b;
  --color-neutral-200: #334155;
  --color-neutral-300: #475569;
  --color-neutral-400: #64748b;
  --color-neutral-500: #94a3b8;
  --color-neutral-600: #cbd5e1;
  --color-neutral-700: #e2e8f0;
  --color-neutral-800: #f1f5f9;
  --color-neutral-900: #f8fafc;
}

/* Global smooth scrolling and modern fundamentals */
html {
  scroll-behavior: smooth;
  font-size: 16px; /* Ensures consistent rem calculations */
}

body {
  font-family: var(--font-primary);
  font-weight: var(--font-normal);
  font-size: var(--text-base);
  line-height: var(--leading-relaxed);
  letter-spacing: -0.011em; /* Slight negative letter spacing for better readability */
  font-feature-settings: 'kern' 1, 'liga' 1, 'calt' 1;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--bg-gradient-light);
  color: var(--color-neutral-700);
  transition: background var(--transition-normal), color var(--transition-normal);
}

body.dark-theme {
  background: var(--bg-gradient-dark);
  color: var(--color-neutral-300);
}

/* Enhanced Focus Management */
.using-keyboard *:focus {
  outline: var(--focus-ring-width) solid var(--focus-ring-color);
  outline-offset: var(--focus-ring-offset);
  border-radius: 0.375rem;
}

.using-mouse *:focus {
  outline: none;
}

/* Focus visible states */
.focus-visible-keyboard:focus {
  outline: var(--focus-ring-width) solid var(--focus-ring-color);
  outline-offset: var(--focus-ring-offset);
  border-radius: 0.375rem;
}

.focus-visible-mouse:focus {
  outline: none;
}

/* Skip to main content link */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.focus\:not-sr-only:focus {
  position: static;
  width: auto;
  height: auto;
  padding: 0.5rem 1rem;
  margin: 0;
  overflow: visible;
  clip: auto;
  white-space: normal;
}

/* Enhanced button focus states */
button:focus-visible,
[role="button"]:focus-visible {
  outline: var(--focus-ring-width) solid var(--focus-ring-color);
  outline-offset: var(--focus-ring-offset);
  border-radius: 0.375rem;
}

/* Enhanced link focus states */
a:focus-visible {
  outline: var(--focus-ring-width) solid var(--focus-ring-color);
  outline-offset: var(--focus-ring-offset);
  border-radius: 0.375rem;
  text-decoration: none;
}

/* Typography Base Styles */
h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-primary);
  font-weight: var(--font-bold);
  line-height: var(--leading-tight);
  letter-spacing: -0.025em;
  color: var(--color-neutral-900);
  margin: 0;
}

h1 {
  font-size: var(--text-4xl);
  font-weight: var(--font-extrabold);
  line-height: var(--leading-tight);
}

h2 {
  font-size: var(--text-3xl);
  font-weight: var(--font-bold);
}

h3 {
  font-size: var(--text-2xl);
  font-weight: var(--font-semibold);
}

h4 {
  font-size: var(--text-xl);
  font-weight: var(--font-semibold);
}

h5 {
  font-size: var(--text-lg);
  font-weight: var(--font-medium);
}

h6 {
  font-size: var(--text-base);
  font-weight: var(--font-medium);
}

p {
  font-family: var(--font-secondary);
  font-size: var(--text-base);
  line-height: var(--leading-relaxed);
  color: var(--color-neutral-700);
  margin: 0;
}

/* Enhanced button typography */
button {
  font-family: var(--font-primary);
  font-weight: var(--font-medium);
  letter-spacing: -0.005em;
}

/* Input and form elements */
input, textarea, select {
  font-family: var(--font-primary);
  font-size: var(--text-base);
  line-height: var(--leading-normal);
}

/* Code elements */
code, pre {
  font-family: var(--font-mono);
  font-feature-settings: 'liga' 1, 'calt' 1;
}

/* Enhanced link typography */
a {
  font-weight: var(--font-medium);
  text-decoration: none;
  color: var(--color-primary-600);
  transition: color var(--transition-fast);
}

a:hover {
  color: var(--color-primary-700);
}

/* Dark theme typography adjustments */
.dark-theme h1,
.dark-theme h2,
.dark-theme h3,
.dark-theme h4,
.dark-theme h5,
.dark-theme h6 {
  color: var(--color-neutral-100);
}

.dark-theme p {
  color: var(--color-neutral-300);
}

.dark-theme a {
  color: var(--color-primary-400);
}

.dark-theme a:hover {
  color: var(--color-primary-300);
}

/* Enhanced animations with better timing */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes bounce-soft {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-8px);
  }
}

@keyframes glow-pulse {
  0%, 100% {
    box-shadow: 0 0 20px rgba(99, 102, 241, 0.2);
  }
  50% {
    box-shadow: 0 0 30px rgba(99, 102, 241, 0.4), 0 0 40px rgba(168, 85, 247, 0.2);
  }
}

@keyframes shimmer-enhanced {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}

@keyframes float-gentle {
  0%, 100% {
    transform: translateY(0) rotate(0deg);
  }
  33% {
    transform: translateY(-6px) rotate(0.5deg);
  }
  66% {
    transform: translateY(3px) rotate(-0.5deg);
  }
}

@keyframes slide-up {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes slide-down {
  from {
    transform: translateY(-100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes zoom-in {
  from {
    transform: scale(0.9);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

/* Animation classes */
.animate-fadeIn {
  animation: fadeIn 0.6s var(--transition-normal);
}

.animate-slideIn {
  animation: slideIn 0.5s var(--transition-normal);
}

.animate-scaleIn {
  animation: scaleIn 0.4s var(--transition-normal);
}

.animate-bounce-soft {
  animation: bounce-soft 2s infinite ease-in-out;
}

.animate-glow-pulse {
  animation: glow-pulse 3s infinite ease-in-out;
}

.animate-shimmer {
  animation: shimmer-enhanced 2s linear infinite;
}

.animate-float-gentle {
  animation: float-gentle 8s ease-in-out infinite;
}

.animate-slide-up {
  animation: slide-up 0.3s ease-out;
}

.animate-slide-down {
  animation: slide-down 0.3s ease-out;
}

.animate-zoom-in {
  animation: zoom-in 0.2s ease-out;
}

/* Toast notification specific animations */
@keyframes toast-slide-in-right {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes toast-slide-out-right {
  from {
    transform: translateX(0);
    opacity: 1;
  }
  to {
    transform: translateX(100%);
    opacity: 0;
  }
}

@keyframes toast-slide-in-left {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes toast-slide-out-left {
  from {
    transform: translateX(0);
    opacity: 1;
  }
  to {
    transform: translateX(-100%);
    opacity: 0;
  }
}

/* Performance optimizations */
.will-change-transform {
  will-change: transform;
}

.will-change-opacity {
  will-change: opacity;
}

.will-change-auto {
  will-change: auto;
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  *, *::before, *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
  
  .animate-bounce-soft,
  .animate-glow-pulse,
  .animate-float-gentle {
    animation: none;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  :root {
    --focus-ring-width: 3px;
    --focus-ring-color: #000000;
  }
  
  .dark {
    --focus-ring-color: #ffffff;
  }
  
  button, a, [role="button"] {
    border: 2px solid currentColor;
  }
}

/* Enhanced tag styling with new color system */
.tag {
  @apply inline-flex items-center px-3 py-1.5 rounded-full cursor-pointer select-none relative overflow-hidden;
  font-family: var(--font-primary);
  font-size: var(--text-sm);
  font-weight: var(--font-medium);
  letter-spacing: -0.005em;
  background: linear-gradient(135deg, var(--color-primary-50) 0%, var(--color-secondary-50) 100%);
  border: 1px solid var(--color-primary-200);
  color: var(--color-primary-700);
  box-shadow: var(--shadow-primary);
  transition: all var(--transition-fast);
}

.tag::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.5s ease;
}

.tag:hover {
  transform: translateY(-1px) scale(1.02);
  box-shadow: var(--shadow-secondary);
  border-color: var(--color-primary-300);
  background: linear-gradient(135deg, var(--color-primary-100) 0%, var(--color-secondary-100) 100%);
}

.tag:focus-visible {
  outline: var(--focus-ring-width) solid var(--focus-ring-color);
  outline-offset: var(--focus-ring-offset);
}

.tag:hover::before {
  left: 100%;
}

.tag:active {
  transform: translateY(0) scale(0.98);
}

/* Enhanced tag variants */
.tag-primary {
  background: var(--gradient-primary);
  color: white;
  border: 1px solid var(--color-primary-500);
  box-shadow: var(--shadow-accent);
}

.tag-secondary {
  background: linear-gradient(135deg, var(--color-neutral-600) 0%, var(--color-neutral-700) 100%);
  color: white;
  border: 1px solid var(--color-neutral-500);
  box-shadow: var(--shadow-primary);
}

.tag-success {
  background: linear-gradient(135deg, var(--color-accent-emerald) 0%, var(--color-accent-emerald-dark) 100%);
  color: white;
  border: 1px solid var(--color-accent-emerald-light);
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.2);
}

.tag-warning {
  background: linear-gradient(135deg, var(--color-accent-orange) 0%, var(--color-accent-orange-dark) 100%);
  color: white;
  border: 1px solid var(--color-accent-orange-light);
  box-shadow: 0 4px 12px rgba(249, 115, 22, 0.2);
}

.tag-danger {
  background: linear-gradient(135deg, var(--color-accent-rose) 0%, var(--color-accent-rose-dark) 100%);
  color: white;
  border: 1px solid var(--color-accent-rose-light);
  box-shadow: 0 4px 12px rgba(244, 63, 94, 0.2);
}

.tag-info {
  background: linear-gradient(135deg, var(--color-primary-500) 0%, var(--color-primary-600) 100%);
  color: white;
  border: 1px solid var(--color-primary-400);
  box-shadow: var(--shadow-secondary);
}

/* Interactive elements */
.interactive-hover {
  transition: all var(--transition-fast);
}

.interactive-hover:hover {
  transform: translateY(-1px);
  box-shadow: var(--shadow-secondary);
}

.focus-visible {
  outline: var(--focus-ring-width) solid var(--focus-ring-color);
  outline-offset: var(--focus-ring-offset);
  border-radius: 4px;
}

/* Improved scrollbar */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--color-neutral-100);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: var(--gradient-primary);
  border-radius: 4px;
  transition: background var(--transition-fast);
}

::-webkit-scrollbar-thumb:hover {
  background: var(--gradient-secondary);
}

/* Enhanced loading states */
.skeleton {
  background: linear-gradient(90deg, var(--color-neutral-200) 25%, var(--color-neutral-100) 37%, var(--color-neutral-200) 63%);
  background-size: 400% 100%;
  animation: shimmer-enhanced 1.4s ease-in-out infinite;
}

.loading-overlay {
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(4px);
}

.dark-theme .loading-overlay {
  background: rgba(15, 23, 42, 0.8);
}

/* Source references - enhanced */
.source-reference {
  font-family: var(--font-secondary);
  font-size: var(--text-sm);
  font-weight: var(--font-medium);
  font-style: italic;
  color: var(--color-neutral-600);
  margin: 0.75rem 0;
  padding: 0.5rem 1rem;
  background: var(--color-neutral-50);
  border-left: 3px solid var(--color-neutral-300);
  border-radius: 0 0.375rem 0.375rem 0;
  transition: all var(--transition-fast);
  line-height: var(--leading-normal);
}

.source-reference:hover {
  background: var(--color-neutral-100);
  border-left-color: var(--color-primary-300);
  transform: translateY(-1px);
  box-shadow: var(--shadow-secondary);
}

/* Subsection titles - improved */
.subsection-title {
  font-family: var(--font-primary);
  font-size: var(--text-lg);
  font-weight: var(--font-semibold);
  color: var(--color-neutral-700);
  margin-top: 1.5rem;
  margin-bottom: 0.75rem;
  position: relative;
  padding-left: 1rem;
  letter-spacing: -0.01em;
  line-height: var(--leading-snug);
}

.subsection-title::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 4px;
  height: 1.2em;
  background: linear-gradient(to bottom, #6366f1, #8b5cf6);
  border-radius: 2px;
}

/* Read more links - enhanced */
.read-more-link {
  font-family: var(--font-primary);
  font-size: var(--text-base);
  font-weight: var(--font-medium);
  display: inline-flex;
  align-items: center;
  margin-top: 0.75rem;
  color: var(--color-primary-600);
  text-decoration: none;
  transition: all var(--transition-fast);
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  background: rgba(99, 102, 241, 0.05);
  letter-spacing: -0.005em;
}

.read-more-link:hover {
  color: var(--color-primary-700);
  background: rgba(99, 102, 241, 0.1);
  transform: translateX(4px);
}

.read-more-link::after {
  content: "→";
  margin-left: 0.5rem;
  transition: transform 0.2s ease;
}

.read-more-link:hover::after {
  transform: translateX(4px);
}

/* Enhanced prose styling with new typography system */
.prose {
  font-family: var(--font-secondary);
  max-width: none;
  color: var(--color-neutral-700);
}

.prose h1 {
  font-family: var(--font-primary);
  font-size: var(--text-4xl);
  font-weight: var(--font-extrabold);
  line-height: var(--leading-tight);
  letter-spacing: -0.025em;
  color: var(--color-neutral-900);
  margin: 2.5rem 0 1.5rem 0;
  padding-bottom: 0.75rem;
  border-bottom: 2px solid var(--color-neutral-200);
  position: relative;
}

.prose h1::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 60px;
  height: 2px;
  background: var(--gradient-primary);
  border-radius: 1px;
}

.prose h2 {
  font-family: var(--font-primary);
  font-size: var(--text-3xl);
  font-weight: var(--font-bold);
  line-height: var(--leading-tight);
  letter-spacing: -0.02em;
  color: var(--color-neutral-800);
  margin: 2rem 0 1rem 0;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid var(--color-neutral-200);
  position: relative;
  padding-left: 1rem;
}

.prose h2::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 4px;
  height: 1.5rem;
  background: var(--gradient-primary);
  border-radius: 2px;
}

.prose h3 {
  font-family: var(--font-primary);
  font-size: var(--text-2xl);
  font-weight: var(--font-semibold);
  line-height: var(--leading-snug);
  letter-spacing: -0.015em;
  color: var(--color-neutral-700);
  margin: 1.75rem 0 0.75rem 0;
}

.prose h4 {
  font-family: var(--font-primary);
  font-size: var(--text-xl);
  font-weight: var(--font-semibold);
  line-height: var(--leading-snug);
  color: var(--color-neutral-600);
  margin: 1.5rem 0 0.5rem 0;
}

.prose h5 {
  font-family: var(--font-primary);
  font-size: var(--text-lg);
  font-weight: var(--font-medium);
  line-height: var(--leading-normal);
  color: var(--color-neutral-600);
  margin: 1.25rem 0 0.5rem 0;
}

.prose h6 {
  font-family: var(--font-primary);
  font-size: var(--text-base);
  font-weight: var(--font-medium);
  line-height: var(--leading-normal);
  color: var(--color-neutral-600);
  margin: 1rem 0 0.5rem 0;
}

.prose p {
  font-family: var(--font-secondary);
  font-size: var(--text-base);
  line-height: var(--leading-relaxed);
  color: var(--color-neutral-700);
  margin-bottom: 1.25rem;
  letter-spacing: -0.003em;
}

.prose strong {
  font-weight: var(--font-semibold);
  color: var(--color-neutral-900);
}

.prose em {
  font-style: italic;
  color: var(--color-primary-600);
  font-weight: var(--font-medium);
}

.prose small {
  font-size: var(--text-sm);
  color: var(--color-neutral-500);
}

.prose blockquote {
  font-family: var(--font-secondary);
  font-size: var(--text-lg);
  line-height: var(--leading-relaxed);
  font-style: italic;
  border-left: 4px solid var(--color-primary-300);
  padding: 1.5rem 2rem;
  margin: 2rem 0;
  background: linear-gradient(135deg, var(--color-primary-50), var(--color-neutral-50));
  border-radius: 0 0.75rem 0.75rem 0;
  color: var(--color-neutral-700);
  position: relative;
  box-shadow: var(--shadow-primary);
}

.prose blockquote::before {
  content: '"';
  position: absolute;
  top: -0.25rem;
  left: 1.5rem;
  font-size: 3.5rem;
  color: var(--color-primary-300);
  font-family: Georgia, serif;
  line-height: 1;
}

.prose code {
  font-family: var(--font-mono);
  font-size: var(--text-sm);
  font-weight: var(--font-medium);
  background: var(--color-neutral-100);
  color: var(--color-primary-700);
  padding: 0.125rem 0.375rem;
  border-radius: 0.375rem;
  border: 1px solid var(--color-neutral-200);
  letter-spacing: -0.01em;
}

.prose pre {
  font-family: var(--font-mono);
  font-size: var(--text-sm);
  line-height: var(--leading-relaxed);
  background: var(--color-neutral-900);
  color: var(--color-neutral-100);
  padding: 1.5rem;
  border-radius: 0.75rem;
  overflow-x: auto;
  margin: 1.5rem 0;
  box-shadow: var(--shadow-secondary);
  position: relative;
  border: 1px solid var(--color-neutral-700);
}

.prose pre::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--gradient-primary);
  border-radius: 0.75rem 0.75rem 0 0;
}

.prose pre code {
  background: transparent;
  color: inherit;
  padding: 0;
  border: none;
  font-size: inherit;
}

.prose a {
  font-family: var(--font-primary);
  font-weight: var(--font-medium);
  color: var(--color-primary-600);
  text-decoration: none;
  position: relative;
  transition: color var(--transition-fast);
  border-bottom: 1px solid transparent;
}

.prose a:hover {
  color: var(--color-primary-700);
  border-bottom-color: var(--color-primary-700);
}

.prose a::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -1px;
  left: 0;
  background: var(--gradient-primary);
  transform: scaleX(0);
  transition: transform var(--transition-fast);
}

.prose a:hover::after {
  transform: scaleX(1);
}

/* Enhanced list styling */
.prose ul {
  list-style: none;
  padding-left: 0;
  margin: 1.25rem 0;
  font-family: var(--font-secondary);
}

.prose ul li {
  position: relative;
  padding-left: 2rem;
  margin: 0.75rem 0;
  line-height: var(--leading-relaxed);
  color: var(--color-neutral-700);
}

.prose ul li::before {
  content: '';
  position: absolute;
  left: 0.5rem;
  top: 0.75rem;
  width: 6px;
  height: 6px;
  background: var(--color-primary-500);
  border-radius: 50%;
}

.prose ol {
  list-style: none;
  counter-reset: ol-counter;
  padding-left: 0;
  margin: 1.25rem 0;
  font-family: var(--font-secondary);
}

.prose ol li {
  position: relative;
  padding-left: 2.5rem;
  margin: 0.75rem 0;
  counter-increment: ol-counter;
  line-height: var(--leading-relaxed);
  color: var(--color-neutral-700);
}

.prose ol li::before {
  content: counter(ol-counter);
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 1.5rem;
  height: 1.5rem;
  background: var(--color-primary-100);
  color: var(--color-primary-700);
  font-size: var(--text-xs);
  font-weight: var(--font-semibold);
  font-family: var(--font-primary);
  border-radius: 50%;
  border: 1px solid var(--color-primary-200);
}

.prose hr {
  border: 0;
  height: 2px;
  background: var(--gradient-primary);
  border-radius: 1px;
  margin: 3rem 0;
  opacity: 0.6;
}

/* Table of contents styling - enhanced */
.toc-container {
  font-family: var(--font-primary);
  background: linear-gradient(135deg, var(--color-primary-50), var(--color-secondary-50));
  border-radius: 1rem;
  padding: 1.5rem;
  margin: 2rem 0;
  border: 1px solid var(--color-primary-200);
  position: sticky;
  top: 6rem;
  backdrop-filter: blur(10px);
  box-shadow: var(--shadow-primary);
}

.toc-heading {
  font-family: var(--font-primary);
  font-size: var(--text-xl);
  font-weight: var(--font-bold);
  color: var(--color-neutral-900);
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  letter-spacing: -0.015em;
}

.toc-heading::before {
  content: '📋';
  margin-right: 0.5rem;
}

.toc-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.toc-item {
  display: flex;
  margin: 0.75rem 0;
  padding-left: 0.5rem;
  transition: all 0.2s ease;
  border-radius: 0.5rem;
}

.toc-item:hover {
  background: rgba(255, 255, 255, 0.5);
  transform: translateX(4px);
}

.toc-number {
  font-family: var(--font-primary);
  color: var(--color-primary-600);
  font-weight: var(--font-semibold);
  margin-right: 0.75rem;
  min-width: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(99, 102, 241, 0.1);
  border-radius: 50%;
  width: 1.5rem;
  height: 1.5rem;
  font-size: var(--text-xs);
}

.toc-link {
  font-family: var(--font-primary);
  font-size: var(--text-sm);
  font-weight: var(--font-medium);
  color: var(--color-neutral-700);
  text-decoration: none;
  flex: 1;
  line-height: var(--leading-normal);
  transition: color var(--transition-fast);
  letter-spacing: -0.005em;
}

.toc-link:hover {
  color: var(--color-primary-600);
}

.toc-link.active {
  color: var(--color-primary-600);
  font-weight: var(--font-semibold);
}

.toc-subtitle {
  font-family: var(--font-secondary);
  color: var(--color-neutral-500);
  font-size: var(--text-xs);
  margin-top: 0.25rem;
  font-weight: var(--font-normal);
}

.toc-level-3 {
  padding-left: 2rem;
}

.toc-level-4 {
  padding-left: 3rem;
}

/* Section highlighting */
.section-highlight {
  padding: 1.5rem;
  border-radius: 0.75rem;
  transition: all 0.3s ease;
  scroll-margin-top: 6rem;
}

.section-highlight:target {
  background: linear-gradient(135deg, #fef3c7, #fde68a);
  box-shadow: 0 0 0 4px rgba(251, 191, 36, 0.2);
  animation: highlight 2s ease-out;
}

@keyframes highlight {
  0% {
    background: rgba(251, 191, 36, 0.3);
  }
  100% {
    background: transparent;
  }
}

/* Back to top button */
.back-to-top {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  background: linear-gradient(135deg, #6366f1, #8b5cf6);
  color: white;
  border: none;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 4px 12px rgba(99, 102, 241, 0.4);
  backdrop-filter: blur(10px);
  z-index: 40;
}

.back-to-top:hover {
  transform: translateY(-2px) scale(1.1);
  box-shadow: 0 8px 20px rgba(99, 102, 241, 0.6);
}

.back-to-top svg {
  width: 1.25rem;
  height: 1.25rem;
  transition: transform 0.2s ease;
}

.back-to-top:hover svg {
  transform: translateY(-2px);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  html {
    font-size: 14px; /* Smaller base font size on mobile */
  }
  
  body {
    line-height: var(--leading-loose); /* More relaxed line height on mobile for better readability */
  }
  
  h1 {
    font-size: var(--text-3xl);
    line-height: var(--leading-tight);
  }
  
  h2 {
    font-size: var(--text-2xl);
  }
  
  h3 {
    font-size: var(--text-xl);
  }
  
  .prose h1 {
    font-size: var(--text-3xl);
    margin: 2rem 0 1rem 0;
  }
  
  .prose h2 {
    font-size: var(--text-2xl);
    margin: 1.5rem 0 0.75rem 0;
  }
  
  .prose h3 {
    font-size: var(--text-xl);
    margin: 1.25rem 0 0.5rem 0;
  }
  
  .prose p {
    font-size: var(--text-base);
    line-height: var(--leading-loose); /* Better mobile reading experience */
    margin-bottom: 1.5rem;
  }
  
  .prose blockquote {
    font-size: var(--text-base);
    padding: 1rem 1.5rem;
    margin: 1.5rem 0;
  }
  
  .prose code {
    font-size: var(--text-xs);
  }
  
  .prose pre {
    font-size: var(--text-xs);
    padding: 1rem;
  }
  
  .toc-container {
    position: relative;
    top: 0;
    margin: 1.5rem 0;
    padding: 1rem;
    font-size: var(--text-sm);
  }
  
  .toc-heading {
    font-size: var(--text-lg);
    margin-bottom: 0.75rem;
  }
  
  .toc-link {
    font-size: var(--text-sm);
    line-height: var(--leading-relaxed);
  }
  
  .back-to-top {
    bottom: 1rem;
    right: 1rem;
    width: 2.5rem;
    height: 2.5rem;
  }
  
  .back-to-top svg {
    width: 1rem;
    height: 1rem;
  }
  
  /* Tag responsive adjustments */
  .tag {
    font-size: var(--text-xs);
    padding: 0.375rem 0.75rem;
  }
  
  .tag-lg {
    font-size: var(--text-sm);
    padding: 0.5rem 1rem;
  }
  
  .tag-xl {
    font-size: var(--text-base);
    padding: 0.75rem 1.25rem;
  }
}

