  animation: shimmer 1.4s ease-in-out infinite;
}

/* Print styles */
@media print {
  .toc-container,
  .back-to-top,
  header,
  footer {
    display: none !important;
  }
  
  .prose {
    font-size: 12pt;
    line-height: 1.5;
  }
  
  .prose h1, .prose h2, .prose h3 {
    break-after: avoid;
  }
  
  .prose blockquote, .prose pre {
    break-inside: avoid;
  }
}

/* Enhanced tag variants with close button */
.tag-closable {
  padding-right: 2.5rem;
}

.tag-close {
  @apply absolute right-2 top-1/2 transform -translate-y-1/2 w-5 h-5 rounded-full flex items-center justify-center transition-all duration-200;
  background: rgba(0, 0, 0, 0.1);
  opacity: 0.6;
}

.tag-close:hover {
  background: rgba(0, 0, 0, 0.2);
  opacity: 1;
  transform: translateY(-50%) scale(1.1);
}

/* Tag size variants */
.tag-sm {
  @apply px-2.5 py-1 text-xs;
}

.tag-lg {
  @apply px-4 py-2 text-base;
}

.tag-xl {
  @apply px-6 py-3 text-lg;
}

/* Enhanced source reference styling */
.source-reference {
  @apply inline-flex items-center px-3 py-1.5 rounded-lg text-sm font-medium transition-all duration-200;
  background: var(--color-primary-50);
  color: var(--color-primary-700);
  border: 1px solid var(--color-primary-200);
  box-shadow: var(--shadow-primary);
}

.source-reference:hover {
  background: var(--color-primary-100);
  border-color: var(--color-primary-300);
  transform: translateY(-1px);
  box-shadow: var(--shadow-secondary);
}

/* Enhanced subsection titles */
.subsection-title {
  @apply text-xl font-bold mb-4 flex items-center;
  color: var(--color-neutral-800);
  border-bottom: 2px solid var(--color-primary-100);
  padding-bottom: 8px;
}

.subsection-title::before {
  content: '';
  width: 4px;
  height: 20px;
  background: var(--gradient-primary);
  border-radius: 2px;
  margin-right: 12px;
}

/* Enhanced read more links */
.read-more-link {
  @apply inline-flex items-center font-semibold transition-all duration-300;
  color: var(--color-primary-600);
  text-decoration: none;
}

.read-more-link:hover {
  color: var(--color-primary-700);
  transform: translateX(4px);
}

.read-more-link::after {
  content: '→';
  margin-left: 8px;
  transition: transform var(--transition-fast);
}

.read-more-link:hover::after {
  transform: translateX(4px);
}

/* Enhanced prose styling */
.prose h1 {
  @apply text-4xl font-bold mb-6 leading-tight;
  color: var(--color-neutral-900);
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.prose h1::after {
  content: '';
  display: block;
  width: 60px;
  height: 4px;
  background: var(--gradient-primary);
  border-radius: 2px;
  margin-top: 16px;
  margin-bottom: 8px;
}

.prose h2 {
  @apply text-2xl font-bold mt-8 mb-4;
  color: var(--color-neutral-800);
  position: relative;
  padding-left: 20px;
}

.prose h2::before {
  content: '';
  position: absolute;
  left: 0;
  top: 4px;
  width: 4px;
  height: 20px;
  background: var(--gradient-primary);
  border-radius: 2px;
}

.prose h3 {
  @apply text-xl font-semibold mt-6 mb-3;
  color: var(--color-neutral-700);
}

.prose h4 {
  @apply text-lg font-semibold mt-4 mb-2;
  color: var(--color-neutral-600);
}

.prose p {
  @apply mb-4 leading-relaxed;
  color: var(--color-neutral-700);
}

.prose strong {
  color: var(--color-neutral-900);
  font-weight: 600;
}

.prose em {
  color: var(--color-primary-600);
  font-style: italic;
}

.prose ul {
  @apply list-none mb-4 space-y-2;
  padding-left: 0;
}

.prose ul li {
  @apply relative pl-6;
  color: var(--color-neutral-700);
}

.prose ul li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 10px;
  width: 6px;
  height: 6px;
  background: var(--color-primary-500);
  border-radius: 50%;
}

.prose ol {
  @apply list-none mb-4 space-y-2;
  counter-reset: list-counter;
  padding-left: 0;
}

.prose ol li {
  @apply relative pl-8;
  counter-increment: list-counter;
  color: var(--color-neutral-700);
}

.prose ol li::before {
  content: counter(list-counter);
  position: absolute;
  left: 0;
  top: 0;
  width: 24px;
  height: 24px;
  background: var(--color-primary-100);
  color: var(--color-primary-700);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  font-weight: 600;
}

.prose a {
  color: var(--color-primary-600);
  text-decoration: none;
  font-weight: 500;
  position: relative;
  transition: color var(--transition-fast);
}

.prose a:hover {
  color: var(--color-primary-700);
}

.prose a::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -2px;
  left: 0;
  background: var(--gradient-primary);
  transform: scaleX(0);
  transition: transform var(--transition-fast);
}

.prose a:hover::after {
  transform: scaleX(1);
}

.prose blockquote {
  @apply border-l-4 pl-6 py-2 my-6 italic;
  border-color: var(--color-primary-300);
  background: var(--color-primary-50);
  color: var(--color-neutral-700);
  border-radius: 0 8px 8px 0;
}

.prose blockquote::before {
  content: '"';
  font-size: 3rem;
  color: var(--color-primary-300);
  position: absolute;
  margin-left: -20px;
  margin-top: -10px;
}

.prose code {
  font-family: 'SF Mono', 'Monaco', 'Cascadia Code', 'Roboto Mono', monospace;
  background: linear-gradient(135deg, #f3f4f6, #e5e7eb);
  color: #4f46e5;
  padding: 0.25rem 0.5rem;
  border-radius: 0.375rem;
  font-size: 0.875rem;
  border: 1px solid #e5e7eb;
}

.prose pre {
  background: linear-gradient(135deg, #1f2937, #374151);
  color: #f9fafb;
  padding: 1.5rem;
  border-radius: 0.75rem;
  overflow-x: auto;
  margin: 1.5rem 0;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: relative;
}

.prose pre::before {
  content: '';
  position: absolute;
  top: 1rem;
  left: 1rem;
  width: 12px;
  height: 12px;
  background: #ef4444;
  border-radius: 50%;
  box-shadow: 20px 0 0 #f59e0b, 40px 0 0 #10b981;
}

.prose pre code {
  background: transparent;
  color: inherit;
  padding: 0;
  border: none;
  font-size: 0.875rem;
}

.prose hr {
  border: 0;
  height: 1px;
  background: linear-gradient(to right, transparent, #e5e7eb, transparent);
  margin: 3rem 0;
}

/* Enhanced dark theme styles */
.dark-theme .prose h1,
.dark-theme .prose h2,
.dark-theme .prose h3,
.dark-theme .prose h4 {
  color: var(--color-neutral-100);
}

.dark-theme .prose p,
.dark-theme .prose li {
  color: var(--color-neutral-300);
}

.dark-theme .prose strong {
  color: var(--color-neutral-100);
}

.dark-theme .prose code {
  background: var(--color-neutral-800);
  color: var(--color-primary-400);
  border-color: var(--color-neutral-700);
}

.dark-theme .prose blockquote {
  background: rgba(99, 102, 241, 0.1);
  border-color: var(--color-primary-600);
  color: var(--color-neutral-300);
}

.dark-theme .prose a {
  color: var(--color-primary-400);
}

.dark-theme .prose a:hover {
  color: var(--color-primary-300);
  border-bottom-color: var(--color-primary-300);
}

.dark-theme .tag {
  background: rgba(99, 102, 241, 0.1);
  border-color: var(--color-primary-600);
  color: var(--color-primary-300);
}

.dark-theme .tag:hover {
  background: rgba(99, 102, 241, 0.2);
  border-color: var(--color-primary-500);
}

.dark-theme .source-reference {
  background: rgba(99, 102, 241, 0.1);
  border-color: var(--color-primary-600);
  color: var(--color-primary-300);
}

.dark-theme .source-reference:hover {
  background: rgba(99, 102, 241, 0.2);
  border-color: var(--color-primary-500);
}

.dark-theme .subsection-title {
  color: var(--color-neutral-200);
  border-color: var(--color-primary-900);
}

.dark-theme ::-webkit-scrollbar-track {
  background: var(--color-neutral-800);
}

.dark-theme ::-webkit-scrollbar-thumb {
  background: var(--gradient-primary);
}

.dark-theme ::-webkit-scrollbar-thumb:hover {
  background: var(--gradient-secondary);
}

/* Print styles */
@media print {
  .back-to-top,
  .header,
  .footer {
    display: none !important;
  }

  .prose {
    font-size: 12pt;
    line-height: 1.4;
  }

  .prose h1, .prose h2, .prose h3 {
    break-after: avoid;
  }

  .prose blockquote, .prose pre {
    break-inside: avoid;
  }
}

